@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@500&display=swap);
body {
  font-family: "Noto Sans KR", sans-serif;
  text-transform: uppercase;
}

.Carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Carousel__wrapper {
  display: flex;
  overflow: hidden;
  scroll-behavior: smooth;
}

.Carousel__list {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
}


.Carousel__button {
  width: 80px;
  height: 40px;
  margin-right: 10px;
  margin-top: 20px;
  background-color: white;
  border: 2px solid #17abdf;
  border-radius: 10%;
  outline: none;
  color: #17abdf;
  text-transform: inherit;
  font-family: inherit;
  font-size: 12px;
  cursor: pointer;
}

